import { Button } from "antd";
import RichEditor from "components/common/RichEditor";
import UploadImage from "components/common/UploadImage";
import React, { useState } from "react";
import { Col, Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { createProductAction } from "../../../state/actions/productActions";

const ProductForm = ({ onCloseForm, ...rest }) => {
  const category = useSelector((state) => state?.category);
  const dispatch = useDispatch();
  const [formValue, setFormValue] = useState({
    code: "",
    price: "",
    name: "",
    description: "",
    categoryId: "",
    image: "",
  });

  const handleChangeEditor = (data) => {
    setFormValue({ ...formValue, description: data });
  };

  const handleSubmitCreateProduct = (e) => {
    e.preventDefault();
    console.log(formValue);
    dispatch(createProductAction({ payload: { ...formValue } }));
  };

  const handleIpChange = (e) => {
    const { name, value } = e.target;
    setFormValue({
      ...formValue,
      [name]: value,
    });
  };

  const handleUpload = (url) => {
    setFormValue({
      ...formValue,
      image: url,
    });
  };

  return (
    <div className="card">
      <div className="card-body">
        <div>
          <i className="fas fa-arrow-left mr-3" onClick={onCloseForm}></i>
          <h5>Thm sn phm</h5>
        </div>
        <hr />
        <div className="mt-4">
          <Form noValidate onSubmit={handleSubmitCreateProduct}>
            <Form.Row>
              <Form.Group as={Col} xl="4" className="px-3">
                <Form.Label>M sn phm</Form.Label>
                <Form.Control
                  name="code"
                  value={formValue.code}
                  type="text"
                  onChange={handleIpChange}
                  placeholder="M sn phm"
                  required
                  autoComplete="off"
                />
                <Form.Control.Feedback type="invalid">Yu cu nhp m sn phm</Form.Control.Feedback>
              </Form.Group>

              <Form.Group as={Col} xl="4" className="px-3">
                <Form.Label>Tn sn phm</Form.Label>
                <Form.Control
                  name="name"
                  value={formValue.name}
                  type="text"
                  onChange={handleIpChange}
                  placeholder="Tn sn phm"
                  required
                  autoComplete="off"
                />
                <Form.Control.Feedback type="invalid">Yu cu nhp m sn phm</Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} xl="4" className="px-3 d-flex align-items-center">
                <Form.Label style={{ flex: "50%" }}>nh sn phm</Form.Label>
                <UploadImage onChange={handleUpload} value={formValue.image} />
              </Form.Group>
              <Form.Group as={Col} xl="6" className="px-3">
                <Form.Label>Gi sn phm</Form.Label>
                <Form.Control
                  name="price"
                  value={formValue.price}
                  type="text"
                  onChange={handleIpChange}
                  placeholder="Gi ca sn phm"
                  required
                  autoComplete="off"
                />
                <Form.Control.Feedback type="invalid">Yu cu nhp m sn phm</Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col} xl="6" className="px-3">
                <Form.Label>Danh mc</Form.Label>
                <Form.Control
                  name="categoryId"
                  onChange={handleIpChange}
                  as="select"
                  value={formValue.categoryId}
                  autoComplete="off"
                >
                  {category?.data?.list?.map((item) => (
                    <option value={item._id} key={item._id}>
                      {item.name}
                    </option>
                  ))}
                </Form.Control>
              </Form.Group>
              <Form.Group as={Col} xl="12" className="px-3">
                <Form.Label>Chi tit sn phm</Form.Label>
                <RichEditor onEditorChange={handleChangeEditor} data={formValue.description} />
              </Form.Group>
            </Form.Row>

            <div className="px-3 float-right">
              <Button htmlType="submit" className="mr-3" type="primary">
                Lu
              </Button>
              <Button danger onClick={onCloseForm}>
                ng
              </Button>
            </div>
          </Form>
        </div>
      </div>
      <div
        dangerouslySetInnerHTML={{
          __html:
            '<blockquote><h2><span style="color:hsl(120, 75%, 60%);">Thng tin sn phm</span></h2></blockquote><p style="margin-left:0px;"><a href="https://www.bachhoaxanh.com/cu/khoai-lang-nhat-tui-1kg"><strong>Khoai lang</strong></a><a href="http://www.bachhoaxanh.com/cu/khoai-lang-nhat-tui-1kg"><strong>&nbsp;Nht</strong></a>&nbsp;l mt loi <a href="https://www.bachhoaxanh.com/cu">c</a> ngon v c rt nhiu loi ging th trong , khoai lang Nht l mt ci tn c l c nhiu ngi yu thch nht. Khoai lang Nht c hnh dng thon, di. Vi lp v bn ngoi mu tm, trong rut th vng, hng v ngt du nh, bi nn chim c rt nhiu cm tnh ca mi ngi.&nbsp;<br>Trong ng y c khoai lang c v ngt, tnh bnh, c cng dng nhun trng, bi b c th, tiu vim, li mt, sng mt,.. c bit <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/loi-ich-bat-ngo-tu-viec-an-sang-bang-khoai-lang-1136839">n khoai vo bui sng</a> s gip bn cung cp y  dinh dng cho c th, c bit l cha c nhiu bnh nguy him m bn khng ng ti.<br>&nbsp;</p><figure class="image"><img src="https://i.ytimg.com/vi/Vwk0mr6jIr0/maxresdefault.jpg" alt="Mn Nht] 2 loi khoai lang Nht nng ngon khng th chi t - YouTube"></figure><h2 style="margin-left:0px;">Li ch tuyt vi ca khoai lang</h2><p style="margin-left:0px;">Trong khoai lang Nht cha rt nhiu vitamin, cht x v giu khong cht gip b sung  cc dng cht cn thit cho c th. Chnh v th, ngi Nht nh gi rt cao loi c ny.<br>Trung bnh trong mi c khoai lang c cha khong 26g tinh bt v ch bng 1/2 lng tinh bt c trong c khoai ty v 1/3 tinh bt trong cm trng. Lng tinh bt va  ny c th gip ngi n no lu m khng cn hp th qu nhiu thc n khc, rt tt i vi nhng ngi <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/tac-dung-cua-khoai-lang-doi-voi-viec-giam-can-996353">dng khoai lang  gim cn</a>.<br>Khoai c xem l loi<strong> thc phm vng</strong> i vi ngi cao tui v tr nh v trong khoai lang Nht cha rt nhiu vitamin v cht x h tr cho h tiu ha hot ng n nh. n mt vi c khoai lang mi ngy s gip c th ci thin bnh tiu ng, chng li bnh ung th, gim vim v tt cho ngi bnh huyt p.</p><h2 style="margin-left:0px;">Cc mn n ngon vi khoai lang</h2><p style="margin-left:0px;">Khoai lang c th ch bin thnh mt s mn n ngon nh: <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/cach-chon-va-luoc-khoai-lang-mat-ngon-nhieu-mat-1256326">khoai lang luc</a>, <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/he-lo-cach-nuong-khoai-lang-bang-lo-vi-song-khong-bi-suong-1083626">khoai lang nng</a>, <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/cach-lam-khoai-lang-ken-thom-ngon-khien-be-me-tit-993077">khoai lang kn</a>, <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/cach-lam-banh-khoai-lang-chien-gion-trong-beo-ngoai-gion-thom-ngon-va-hap-dan-1262210">khoai lang chin gin</a>, <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/cach-lam-thach-rau-cau-khoai-lang-moi-la-dep-mat-1117910">thch rau cu khoai lang</a>, <a href="https://www.bachhoaxanh.com/kinh-nghiem-hay/cach-nau-che-khoai-lang-deo-ngon-ngot-trang-mieng-cho-ca-nha-1273120">ch khoai lang</a>,...<br><strong>Lu : </strong>Sn phm nhn c c th khc vi hnh nh v mu sc v s lng nhng vn m bo v mt khi lng v cht lng.</p>',
        }}
      ></div>
    </div>
  );
};

export default ProductForm;
