import React, { Component } from "react";
import { withRouter } from "react-router";

class Login extends Component {
  onSubmit = () => {
    this.props.history.push("/admin/product");
  };

  render() {
    return (
      <div className="wrapper-login">
        <div className="container-login">
          <div className="col-left">
            <div className="login-text">
              <img
                src="/assets/image/logo-login.png"
                width="150"
                alt="logo"
              ></img>
              <p>
                Chng ti lun cho n Khch hng ti vn Kadon, ni bn c
                th n tham quan trc tip hoc tri nghim ng hnh cng vn
                canh tc theo m hnh thun t nhin.
              </p>
            </div>
          </div>
          <div className="col-right">
            <div className="login-form">
              <h2>Login</h2>
              <form>
                <p>
                  <input type="text" placeholder="Tn ng nhp" required />
                </p>
                <p>
                  <input type="password" placeholder="Mt khu" required />
                </p>
                <p>
                  <input
                    className="btn"
                    type="button"
                    defaultValue="ng nhp"
                    onClick={this.onSubmit}
                  />
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(Login);
